import React, { useState } from 'react';
import api from './../API/baidu'

import { Upload, message } from 'antd';
import { InboxOutlined } from '@ant-design/icons';
import cnEnGe from './cnEnGe';
const { Dragger } = Upload

// æµ‹è¯•æ•°æ®ï¼š ä¸­è‹±
let data = {"direction":0,"paragraphs_result_num":3,"log_id":6200264271404622000,"words_result_num":9,"words_result":[{"words":"h zu"},{"words":"kÃ¶nnen sie sich vorstellen ?"},{"words":"siehe e ?"},{"words":"darf ich mich vorstellen ?"},{"words":"die er ?"},{"words":"wie heiÃŸen sie ? wie heiÃŸt du ?"},{"words":"entre ? uralte ?"},{"words":"ich heiÃŸe ."},{"words":"er"}],"paragraphs_result":[{"words_result_idx":[0]},{"words_result_idx":[1,2,3,4]},{"words_result_idx":[5,6,7,8]}]}

const transformGeneralBasic = (data) => {
  let result = []
  let words = data.words_result
  words.forEach(item => {
    result.push(item.words)
  })
  return result
}

const json2form = json => {
  let form =  new URLSearchParams()
  Object.entries(json).forEach(item => {
    form.append(item[0], item[1])
  })
  return form
}

let temp = 0 // æµ‹è¯•æ•°æ®ç”¨
let textKey = 0 // æ¯å¼ å›¾çš„é”®
let textDict = {} // æ‰€æœ‰ä»¥è¯†åˆ«çš„æ–‡æœ¬ï¼Œç”¨å¯¹è±¡æ˜¯é˜²æ­¢å‡½æ•°é‡é€‰å¤±å»å€¼
let hasCopyEvent = false // æ˜¯å¦å·²ç»copy

function Page(text, setText, textLanguage) {
  textDict.text = text

  const [loading, setLoading] = useState(false)

  // æ›´æ–°text
  const updateText =  (text, image) => {
    setLoading(false)
    textKey = textKey + 1 
    setText({
      ...textDict.text,
      [textKey]: {
        words: text,
        image
      }
    })
  }

  
  // å¯ç”¨mockæ•°æ®
  if (temp < 0) {
    textKey = textKey + 1
    setText({
      ...text,
      [textKey]: {
        words: transformGeneralBasic(data),
        image: '33333'
      }
    })
    temp = temp + 1
  }
  
  // ä¸Šä¼ æ“ä½œ
  const upload = (file) => {
    setLoading(true)
    let reader=new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
    let image = this.result
    let images = image.split(',')
    console.log(images)
    if (textLanguage === 'CHN_GER') {
      cnEnGe(images[1], json2form).then(result => {
        updateText(
          result,
          images)
        setLoading(false)  
      }).catch(()=> {
        setLoading(false)
        message.error('å‘ç”Ÿé”™è¯¯ï¼Œè¯·é‡è¯•');
      })
      return
    }


    // æ™®é€šè¯­è¨€ä¸Šä¼ 
    let param = {
      image: encodeURI(images[1]),
      language_type: textLanguage,
      paragraph: 'true',
      probability: 'false',
      recognize_granularity: 'big',
      detect_direction: 'true',
    }    
    let result = json2form(param)
    api.ocr(result).then(resp => {
      updateText(
        transformGeneralBasic(resp.data),
        image)
    }).catch(() => {
      setLoading(true)
      message.error('å‘ç”Ÿé”™è¯¯ï¼Œè¯·é‡è¯•')
    })
    }
    
    return false
  }

  // ä»ç²˜è´´æ¿ä¸­ä¸Šä¼ 
  if (!hasCopyEvent) {
    window.addEventListener("paste", function (e){
      if (loading) return
      setLoading(false)
      let items = e.clipboardData.items
        for (let item of items) {
            if (item.kind === 'file') {
              upload(item.getAsFile())
            }
          }
    })
    hasCopyEvent = true
  }
  

  return (
    <div>
        <Dragger
          name= "image"
          beforeUpload= {upload}
          listType="picture-card"
          multiple= {true}
          showUploadList = {false}
        >
          <p className="ant-upload-drag-icon">
            <InboxOutlined />
          </p>
          <p className="ant-upload-text">
            {loading ? 'ä¸Šä¼ ä¸­ï¼Œè¯·ç¨å...' : 'å•å‡»æˆ–æ‹–æ‹½ä¸Šä¼ å›¾ç‰‡'}
          </p>
          <p>
            æˆ–æŒ‰ Ctrl/Commend + V ä»ç²˜è´´æ¿ä¸Šä¸Šä¼ 
          </p>
        </Dragger>
    </div>
  );
}

export default Page;
